import React, { Component } from 'react';
// import logo from './logo.svg';
import './App.scss';

// components
import Dice from './Dice.js'
import ResultBar from './ResultBar.js'

//assets
import d4 from './d4.png';
import d6 from './d6.png';
import d8 from './d8.png';
import d10 from './d10.png';
import d12 from './d12.png';
import d20 from './d20.png';
import d100 from './d100.png';

// this hash is made because every Dice must get proper image form assets
const hashOfDices = {
  'd4': d4,
  'd6': d6,
  'd8': d8,
  'd10': d10,
  'd12': d12,
  'd20': d20,
  'd100': d100
}

class App extends Component {
  constructor(props) {
    super(props)
    this.state = {
      initialDiceStates: [
        {sides: 4,    value: true},
        {sides: 6,    value: true},
        {sides: 8,    value: true},
        {sides: 10,   value: true},
        {sides: 12,   value: true},
        {sides: 20,   value: true},
        {sides: 100,  value: true}
      ],
      results: []
    };
  }

  onCheck(sides) {
    let dup = [...this.state.initialDiceStates]
    dup.map( (obj) => {
      if (obj.sides === sides) {
        obj.value = !obj.value
      }
      return obj
    })
    this.setState({initialDiceStates: dup })
  }

  updateResults(val) {
    let dup = [...this.state.results];
    dup.unshift(val);
    console.log("updated results", dup);
    this.setState({results: dup})
  }

  deleteFromResults(index) {
    let dup = [...this.state.results]
    dup.splice(index, 1)
    this.setState({results: dup});
  }

  render() {
    return (
      <div className="App">
        {/* <img src={logo} className="App-logo" alt="logo" />*/}

        <div className="CheckboxBar">
          {this.state.initialDiceStates.map( key => {
            return(
              <div className="CheckboxBar-checkbox" key={key.sides}>
                <input type="checkbox" value="true" checked={key.value} id={"input-" + key.sides} onChange={this.onCheck.bind(this, key.sides)} />
                <label htmlFor={"input-" + key.sides} className="CheckboxBar-label"> {key.sides} </label>
              </div>
            )
          })}
        </div>

        <div className="App-main-area">
          {this.state.initialDiceStates.map( key => {
            if (key.value === true) {
              return(
                <Dice
                  key={key.sides}
                  sides={key.sides}
                  updateFnProp={this.updateResults.bind(this)}
                  imageSrc={hashOfDices['d'+key.sides]}
                />
              )
            }
          })}
        </div>

        {/* <Dice sides={6} />
        <Dice sides={8} />
        <Dice sides={10} />
        <Dice sides={12} />
        <Dice sides={20} />
        <Dice sides={100} /> */}

        <ResultBar
        results={this.state.results}
        deleteFnProp={this.deleteFromResults.bind(this)}
        />
      </div>
    );
  }
}

export default App;



