import React, { Component } from 'react';
import './Dice.scss';

class Dice extends Component {
  constructor(props) {
    super(props)
    this.state = {
      result: { val: 0, timestamp: 0 },
      animationInProgress: false
    };
    this.generateRandomResult = this.generateRandomResult.bind(this);
  }

  generateRandomResult(e) {
    e.stopPropagation()
    e.preventDefault()

    let randomVal = Math.ceil(Math.random() * this.props.sides);
    let result = { val: randomVal, timestamp: (new Date).getTime() }

    this.setState({ result: result })
    this.passValueToParent(result)

    this.animateDice()
  }

  passValueToParent(val) {
    this.props.updateFnProp(val);
  }

  animateDice() {
    this.setState({ animationInProgress: true });
    setTimeout(() => {
      this.setState({ animationInProgress: false });
    }, 300)
  }

  render() {
    return (
      <div className="Dice" onClick={this.generateRandomResult} tabIndex="0">
        <div className="Dice-wrapper">
          {/* <div className="Dice-dice"> {this.props.sides} </div>*/}
          <img className={`Dice-image ${this.state.animationInProgress ? 'rotate' : ''}`} src={this.props.imageSrc} />
        </div>
        <div className="Dice-result"> {this.state.result.val} </div>
      </div>
    );
  }
}

export default Dice;



