import React, { Component } from 'react';
import { CSSTransitionGroup } from 'react-transition-group' // ES6
import './ResultBar.scss';

class ResultBar extends Component {
  constructor(props) {
    super(props)
    this.state = {
      results: []
    };
  }

  componentWillReceiveProps(nextProps) {
    console.log("nextProps", nextProps.results)
    this.setState({results: nextProps.results});
  }

  deleteResult(resultIndex) {
    this.props.deleteFnProp(resultIndex);
  }

  render() {
    return (
      <div className="ResultBar">
        <div className="ResultBar-inner">

          <CSSTransitionGroup
            transitionName="example"
            transitionEnterTimeout={200}
            transitionLeaveTimeout={200}>

            {this.state.results.map( (result, index) => {
              return(
                <div className="ResultBar-result" key={result.timestamp}>
                  <div className="ResultBar-content"> {result.val} </div>
                  <button
                    className="ResultBar-button"
                    onClick={this.deleteResult.bind(this, index)}
                  >
                  </button>
                </div>
              )
            })}

          </CSSTransitionGroup>

        </div>
      </div>
    );
  }
}

export default ResultBar;



